
export const apiurl = "http://localhost:8081/";
// export const swaggerurl = "http://fee7-110-235-232-108.ngrok-free.app"

// http://localhost:8081/
import axios from 'axios';

export async function createCandidate(reqData: any) {
  try {
    const response = await axios.post(`${apiurl}contacts/create`, reqData, {
      headers: {
        'Content-Type': 'application/json',
        'X-Requested-With': 'XMLHttpRequest',
      },
    });
    return response.data;  // Axios automatically parses the JSON response
  } catch (err : any) {
    // Axios throws an error for non-2xx status codes, you can access `err.response`
    return err.response ? err.response.data : err.message;
  }
}



export async function uploadResume(reqData: any, candidateId: any) {
	try {
		const response = await fetch(apiurl + '/contacts/resume/' + candidateId, {
			method: 'POST',
			headers: {
				'Content-Type': 'application/json',
				"X-Requested-With": "XMLHttpRequest",
			},
			body: reqData,
		});
		const data = await response.json();
		return data;
	} catch (err) {
		return err;
	}
}

